import React, { Component } from "react";
import { v4 as uuid } from 'uuid';
import Container from "../Container/Container";
import ContactForm from "../ContactForm/ContactForm";
import ContactList from "../ContactList/ContactList";
import Filter from "../Filter/Filter";
import contactsList from "../data/contacts.json";

class App extends Component {
  state = {
    contacts: contactsList,
    filter: '',
    name: '',
    number: '',
  };
  
  addContact = (name, number) => {
    const item = {
       id : uuid(),
      name,
      number,
    }
    const includeName = this.state.contacts.reduce(
    (acc, contact) => [...acc, contact.name],
    [],
    );
    const includeNumber = this.state.contacts.reduce(
    (acc, contact) => [...acc, contact.number],
    [],
    );

    if(name === '' || number === '') {
    alert('Please enter all fields!');
    return;
    }

    if (includeName.includes(name)) {
            alert(`${name} is already in contacts`);
          return;
    } else if (includeNumber.include(number)){
      alert(`${number} is already in contacts`);
          return;
    } else {
      this.setState(({ contacts }) => ({
            contacts: [item, ...contacts],
      }));
    }
  }
  
  changeFilter = event => {
    this.setState({ filter: event.target.value });
  };

  getVisibleContacts = () => {
    const { filter, contacts } = this.state;
    const normalizedFilter = filter.toLowerCase();

    return contacts.filter(contact =>
      contact.name.toLowerCase().includes(normalizedFilter),
    );
  };


  deleteContact = contactId => {
    this.setState(prevState => ({
      contacts: prevState.contacts.filter(contact => contact.id !== contactId),
    }));
  };


  render() {
    const { contacts, filter } = this.state;
    const visibleContacts = this.getVisibleContacts();
    return (
      <>
       <Container>
        <h1 className="phonebook">Phonebook</h1>
        <ContactForm contacts={contacts} onSubmit={this.addContact} />

        <h2 className="contacts">Contacts</h2>
        <Filter value={filter} onChange={this.changeFilter} />
        <ContactList
          contacts={visibleContacts}
          onDeleteContact={this.deleteContact}
        />
      </Container>
      </>
    );
  }
  
}

export default App;